.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
}

.inputArea {
  width: 50vw;
  max-height: 20vh;
  overflow-y: auto;
  padding: 2rem;
  font-size: 2rem;
  border-radius: 1vw;
  border: solid;
}

.inputAreaTitle {
  font-size: 2rem;
  padding: 2rem;
}

.customMean {
  width: 30vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 2rem;
}

.customMeanLabel {
  flex: 1;
  margin-right: 1rem;
  font-size: 1.5rem;
}
.customMeanInput {
  padding: 1rem;
  flex: 1;

  border-style: dotted;
}

.calculateButton {
  padding: 1rem 2rem;
  border-radius: 1vw;
  font-weight: bold;
}

.results {
  width: 40vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  padding: 2rem;
}

.resultNumber {
  font-size: 2rem;
  font-weight: 500;
  padding: 1rem;
}

.resultLabel {
  font-size: 1.5rem;
  font-weight: 400;
  margin-right: 2rem;
}
